/**
 * 描述列表
 */
import React from 'react';
import { Divider } from 'antd';
import { Space } from '@zenlayer/zen-ui';
import classNames from '../utils/classnames';
import { DescriptionsProps, ItemProps } from './interface';

const prefixCls: string = 'zen-descriptions';
const Descriptions: React.FC<DescriptionsProps> = ({
  title,
  column = 3,
  children,
  justifyItems,
  width,
  height,
  className,
  style,
}) => {
  const contentStyle = {
    gridTemplateColumns: `repeat(${column}, 1fr)`,
    justifyItems: justifyItems,
  };
  return (
    <div
      className={classNames(prefixCls, className)}
      style={{ ...style, width: width, height: height }}
    >
      {!!title && <div className={`${prefixCls}-title`}>{title}</div>}
      <div style={contentStyle} className={`${prefixCls}-content`}>
        {children}
      </div>
    </div>
  );
};

const Item: React.FC<ItemProps> = ({
  label,
  columnSpan = 1,
  rowSpan = 1,
  children,
  justifySelf,
  direction = 'vertical',
  width,
  height,
  className,
  style,
}) => {
  const ItemStyle = {
    gridColumnStart: `span ${columnSpan}`,
    gridRowStart: `span ${rowSpan}`,
    justifySelf: justifySelf,
    width: width,
    height: height,
    ...style,
  };
  return (
    <Space
      style={ItemStyle}
      className={classNames(`${prefixCls}-item`, className)}
      direction={direction}
    >
      {label && <div className={`${prefixCls}-item-label`}>{label}</div>}
      {children && <div className={`${prefixCls}-item-text`}>{children}</div>}
    </Space>
  );
};

const Group: React.FC<{
  children: React.ReactElement[] | React.ReactElement;
}> = ({ children }) => {
  return (
    <div className={`${prefixCls}-group`}>
      {Array.isArray(children)
        ? (children as React.ReactElement[]).map((c, i) => {
            return (
              <React.Fragment key={c.key || i}>
                {i > 0 && <Divider />}
                {c}
              </React.Fragment>
            );
          })
        : children}
    </div>
  );
};

export { Descriptions, Group, Item };
